<template>
	<div>
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span></span>
			</div>
			<el-form ref="form" :model="form" :rules="rules" label-position="left" label-width="110px">
				<el-row>
					<el-col :span="5">
						<el-form-item prop="cate_1st" label="">
							<el-select v-model="form.cate_1st" @change="cate_1st_handle" placeholder="">
								<el-option v-for="item in cate_1st_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item prop="cate_2nd">
							<el-select v-model="form.cate_2nd" @change="cate_2nd_handle" placeholder="">
								<el-option v-for="item in cate_2nd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item prop="cate_3rd">
							<el-select v-model="form.cate_3rd" placeholder="">
								<el-option v-for="item in cate_3rd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
				</el-row>
				<el-form-item prop="name" label="">
					<el-col :span="14">
						<el-input v-model.trim="form.name"></el-input>
					</el-col>
					<el-col :span="24" class="tip">3200</el-col>
				</el-form-item>
				<el-form-item prop="hotPoint" label="">
					<el-col :span="14">
						<el-input type="textarea" v-model.trim="form.hotPoint"></el-input>
					</el-col>
					<el-col :span="24" class="tip">140</el-col>
				</el-form-item>
				<el-form-item prop="price" label="">
					<el-col :span="4">
						<el-input v-model.trim.number="form.price">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
					<el-col :span="24" class="tip"></el-col>
				</el-form-item>
				<el-form-item prop="marketPrice" label="">
					<el-col :span="4">
						<el-input v-model.trim.number="form.marketPrice">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
				</el-form-item>
				<el-form-item prop="cost" label="">
					<el-col :span="4">
						<el-input v-model.trim.number="form.cost">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="4">
						<el-input readonly v-model="discount">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip"></el-col>
				</el-form-item>
				<el-form-item prop="inventory" label="">
					<el-col :span="4">
						<el-input v-model.number="form.inventory">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
					<el-col :span="24" class="tip"></el-col>
				</el-form-item>
				<el-form-item prop="articleNo" label="">
					<el-col :span="4">
						<el-input v-model.trim="form.articleNo"></el-input>
					</el-col>
					<el-col :span="24" class="tip"></el-col>
					<el-col :span="24" class="tip">20_/-</el-col>
				</el-form-item>
				<el-form-item prop="img_lg" label="">
					<upload :src.sync="form.img_md" :lg-img.sync="form.img_lg" remove-action="/api/upload" upload-action="/api/upload/goods"></upload>
					<el-col :span="24" class="tip">
						jpgifpng
						800*8001M
					</el-col>
				</el-form-item>
				<el-form-item prop="slider" label="">
					<el-upload :limit="6" list-type="picture-card" :file-list="sliderFileList" action="/api/upload/slider/" :headers="headers" :before-upload="handleBeforeUpload"
					 :on-success="handleSliderSuccess" :on-exceed="handleSliderExceed" :before-remove="handleSliderBeforeRemove"
					 :on-error="handleError" :on-preview="handleCardPreview">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-col :span="24" class="tip">
						jpgifpng
						800*8001M
					</el-col>
				</el-form-item>
				<div class="section-title"></div>
				<el-form-item prop="brand" label="">
					<el-col :span="14">
						<el-input v-model.trim="form.brand"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item prop="detail" label="">
					<div ref="toolbar" class="w-e-toolbar"></div>
					<div ref="editor" class="w-e-text-container"></div>
				</el-form-item>
				<div class="section-title"></div>
				<el-row>
					<el-col :span="5">
						<el-form-item prop="province" label="">
							<el-select v-model="form.province" placeholder="">
								<el-option v-for="item in province_options" :label="item.name" :value="item.province_id" :key="item._id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item prop="city">
							<el-select v-model="form.city" placeholder="">
								<el-option v-for="item in city_options" :label="item.name" :value="item.city_id" :key="item._id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item prop="county">
							<el-select v-model="form.county" placeholder="//">
								<el-option v-for="item in county_options" :label="item.name" :value="item.county_id" :key="item._id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item prop="town">
							<el-select v-model="form.town" placeholder="">
								<el-option v-for="item in town_options" :label="item.name" :value="item.town_id" :key="item._id"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
				</el-row>
				<el-form-item prop="freight" label="">
					<el-col :span="3">
						<el-input v-model.number="form.freight">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="success" @click="handleEdit"></el-button>
				</el-form-item>
			</el-form>
			<!--  -->
			<el-dialog width="30%" :visible.sync="dialogVisible">
				<img width="100%" :src="dialogImageUrl" alt>
			</el-dialog>
		</el-card>
	</div>
</template>
<script>
	import { Upload, Category, Goods, PCCT } from "@/api/index";
	import E from "wangeditor";

	import upload from "@/components/Upload.vue";

	export default {
		props: ["id"],
		components: {
			upload
		},
		data() {
			return {
				fileList: [],
				dialogImageUrl: "",
				dialogVisible: false,
				headers: {
					Authorization: `Bearer ${sessionStorage.token}`
				},
				form: {
					cate_1st: "",
					cate_2nd: "",
					cate_3rd: "",
					hotPoint: "",
					name: "",
					price: "",
					marketPrice: "",
					cost: "",
					discount: "",
					inventory: 1000,
					articleNo: "",
					img_lg: "",
					img_md: "",
					slider: "",
					brand: "",
					detail: "",
					freight: 0,
					province: "",
					city: "",
					county: "",
					town: "",
				},
				rules: {
					cate_1st: [
						{ required: true, message: '', trigger: 'blur' },
					],
					cate_2nd: [
						{ required: true, message: '', trigger: 'blur' },
					],
					cate_3rd: [
						{ required: true, message: '', trigger: 'blur' },
					],
					hotPoint: [
						{ required: false, message: '', trigger: 'blur' },
						{ type: 'string', max: 140, message: '140', trigger: 'blur' },
					],
					name: [
						{ required: true, message: '', trigger: 'blur' },
						{ type: 'string', min: 3, max: 200, message: '3200', trigger: 'blur' },
					],
					price: [
						{ required: true, message: '', trigger: 'blur' },
						{ pattern: /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/, message: '0-999999', trigger: 'blur' },
					],
					marketPrice: [
						{ required: true, type: 'float', message: '', trigger: 'blur' },
						{ pattern: /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/, message: '0-999999', trigger: 'blur' },
					],
					cost: [
						{ required: true, type: 'float', message: '', trigger: 'blur' },
						{ pattern: /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/, message: '0-999999', trigger: 'blur' },
					],
					inventory: [
						{ required: true, type: 'integer', message: '', trigger: 'blur' },
						{ min: 0, max: 999999, type: 'integer', message: '0-999999', trigger: 'blur' },
					],
					articleNo: [
						{ required: true, type: 'string', message: '', trigger: 'blur' },
						{ pattern: /^[\w\u4E00-\u9FA5-./]{1,20}$/, message: '20_/-', trigger: 'blur' },
					],
					img_lg: [
						{ required: true, message: '', trigger: 'blur' },
					],
					slider: [
						{ required: true, message: '', trigger: 'blur' },
					],
					brand: [
						{ required: true, message: '', trigger: 'blur' },
					],
					detail: [
						{ required: true, message: '', trigger: 'blur' },
					],
					freight: [
						{ required: true, type: 'number', message: '', trigger: 'blur' },
						{ min: 0, type: 'number', message: '0-999999', trigger: 'blur' },
					],
					province: [
						{ required: true, message: '', trigger: 'blur' },
					],
					city: [
						{ required: true, message: '', trigger: 'blur' },
					],
					county: [
						{ required: true, message: '', trigger: 'blur' },
					],
					town: [
						{ required: true, message: '', trigger: 'blur' },
					]
				},
				cate_1st_options: [],
				cate_2nd_options: [],
				cate_3rd_options: [],
				province_options: [],
				city_options: [],
				county_options: [],
				town_options: [],
				mainFileList: [],
				sliderFileList: [],
			};
		},
		computed: {
			discount() {
				let discount = ((this.form.price / this.form.marketPrice) * 10).toFixed(2);
				this.form.discount = discount;
				switch (discount) {
					case "NaN":
						return "";
						break;
					case "Infinity":
						return "";
						break;
					default:
						return discount;
						break;
				}
			}
		},
		created() {
			document.title = "";
		},
		async mounted() {
			// 
			let data = await this.getDetail();
			//
			const editor = new E(this.$refs.toolbar, this.$refs.editor);
			editor.customConfig.zIndex = 100;
			//
			editor.customConfig.uploadImgServer = "/api/upload/editor/";
			editor.customConfig.uploadFileName = "file";
			// header
			editor.customConfig.uploadImgHeaders = {
				Authorization: `Bearer ${sessionStorage.token}`
			};
			//HTMLdata
			editor.customConfig.onchange = html => {
				this.form.detail = html;
			};
			editor.create();
			editor.txt.html(data.detail);
			// 
			this.handleCateChange(1, "cate_1st", false);
			this.handleCateChange(data.cate_1st, "cate_2nd", false);
			this.handleCateChange(data.cate_2nd, "cate_3rd", false);
			// 
			if (data.img_md) {
				this.mainFileList = [{
					name: '1.jpg',
					url: data.img_md,
				}];
			}
			// 
			let arr = data.slider.split(',');
			let list = arr.map(function(item, index) {
				let obj = {};
				obj.url = item;
				obj.name = index + '.jpg';
				obj.response = { src: item };
				return obj;
			});
			this.sliderFileList = list;
			// 
			this.loadProvince();
		},
		methods: {
			async getDetail() {
				let { status, data } = await Goods.getDetail({ id: this.id });
				if (status) {
					this.form = data;
					this.form.id = this.id;
					return Promise.resolve(data);
				}
			},
			//change
			cate_1st_handle(id) {
				this.handleCateChange(id, "cate_2nd");
			},
			cate_2nd_handle(id) {
				this.handleCateChange(id, "cate_3rd");
			},
			async handleCateChange(id, cate, setDefault = true) {
				let data = await this.getOptions(id);
				this[cate + "_options"] = data;
				//
				if (data.length === 0) {
					this.form[cate] = undefined;
					return false;
				}
				if (setDefault) {
					//
					this.form[cate] = data[0].id;
				}
			},
			//
			async getOptions(id) {
				let { status, data, msg } = await Category.load({ pId: id });
				if (status) {
					return Promise.resolve(data);
				} else {
					this.$message.error(msg);
				}
			},
			// 
			async loadProvince() {
				let { status, data } = await PCCT.province();
				if (status) {
					this.province_options = data;
					//
					this.form.province = data[0].province_id;
				}
			},
			// id
			async loadCity(id) {
				let { status, data } = await PCCT.city({ id });
				if (status) {
					this.city_options = data;
					//
					this.form.city = data[0].city_id;
				}
			},
			// id
			async loadCounty(id) {
				let { status, data } = await PCCT.county({ id });
				if (status) {
					this.county_options = data;
					//
					this.form.county = data[0].county_id;
				}
			},
			// id
			async loadTown(id) {
				let { status, data } = await PCCT.town({ id });
				if (status) {
					this.town_options = data;
					//
					this.form.town = data[0].town_id;
				}
			},
			handleMainSuccess(response, file, fileList) {
				if (response.status) {
					this.form.img_lg = response.lgImg;
					this.form.img_md = response.mdImg;
				}
			},
			handleError(err, file, fileList) {
				let { status, message } = err;
				switch (status) {
					case 401:
						this.$message.error(`:401,Token,!`);
						break;
					case 400:
						message = JSON.parse(message);
						this.$message.error(`:400,${message.msg}`);
						break;
					default:
						this.$message.error(`:${status},${message}!`);
						break;
				}
			},
			handleBeforeUpload(file) {
				let reg = /^image\/(jpe?g|png)$/;
				const isImg = reg.test(file.type);
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isImg) {
					this.$message.error(" JPG/PNG !");
				}
				if (!isLt2M) {
					this.$message.error(" 2MB!");
				}
				return isImg && isLt2M;
			},
			async handleMainBeforeRemove(file, fileList) {
				try {
					let res1 = await Upload.remove({ src: "." + this.form.img_lg });
					let res2 = await Upload.remove({ src: "." + this.form.img_md });
					if (res1.status && res2.status) {
						//
						this.form.img_lg = "";
						this.form.img_md = "";
						return true;
					} else {
						return false;
					}
				} catch (e) {
					this.$message.error(e);
				}
			},
			handleCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			handleMainExceed(files, fileList) {
				this.$message.warning(
					` 1  ${files.length}  ${files.length + fileList.length} `
				);
			},
			handleSliderSuccess(response, file, fileList) {
				if (response.status) {
					this.form.slider = this.convertFileList(fileList);
				}
			},
			handleSliderExceed(files, fileList) {
				this.$message.warning(
					` 6  ${files.length}  ${files.length + fileList.length} `
				);
			},
			async handleSliderBeforeRemove(file, fileList) {
				let src = file.response.src.replace(/.+\/images/, "./images");

				let { status } = await Upload.remove({ src });

				if (status) {
					let i = fileList.findIndex(item => item.url == file.response.src);
					let copy = [...fileList];
					copy.splice(i, 1);
					this.form.slider = this.convertFileList(copy);
					return true;
				} else {
					return false;
				}
			},
			//
			convertFileList(fileList) {
				let res = [];
				fileList.forEach(function(item, index) {
					res.push(item.response.src);
				});
				return res.toString();
			},
			//
			async handleEdit() {
				let { status } = await Goods.update(this.form);
				if (status) {
					this.$message.success("");
				}
			}
		}
	};
</script>
<style scoped>
	.tip {
		font-size: 12px;
		color: #999;
	}

	.section-title {
		background-color: #f5f5f5;
		padding: 10px;
		margin-bottom: 10px;
	}

	.el-upload img {
		max-width: 100%;
	}

	.w-e-toolbar {
		border: 1px solid #ccc;
		border-bottom: 0;
	}

	.w-e-text-container {
		border: 1px solid #ccc;
		min-height: 300px;
	}
</style>
